.footer {
  padding-top: 20px;
  padding-bottom: 20px;

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    border-bottom: 1px solid #eee;
    padding-bottom: 100px;

    @include desktop {
      flex-wrap: wrap;
      padding-bottom: 50px;
    }
  }

  &__menu-service {
    @include desktop {
      margin-bottom: 40px;
      width: 50%;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__company {
    @include desktop {
      margin-bottom: 40px;
      width: 50%;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__newsletter {
    @include desktop {
      width: 50%;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__menu-title {
    margin-bottom: 30px;
    text-transform: uppercase;

    @include mobile {
      margin-bottom: 15px;
      text-align: center;
    }
  }

  &__menu--flex {
    display: flex;
  }

  &__menu-item {
    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  &__menu-item--flex {
    font-weight: 700;

    &:not(:last-child) {
      margin-right: 45px;
      margin-bottom: 0;
    }
  }

  &__menu-link {
    padding-top: 5px;
    padding-bottom: 5px;
  }

  &__form {
    display: flex;
    justify-content: space-between;
    border-bottom: 3px solid var(--text-color);
    padding-bottom: 15px;
  }

  &__form-input {
    outline: none;
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 25px;
    padding-bottom: 25px;

    .footer__menu {
      margin-bottom: 25px;
    }

    @include mobile {
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  &__currency-lang {
    display: flex;
    align-items: center;
  }

  &__currency {
    display: flex;
    margin-right: 60px;
  }

  &__select-caption {
    margin-right: 15px;
    font-size: 13px;
    line-height: 1.2;
    text-align: left;
    color: var(--text-gray);
  }

  &__lang {
    display: flex;
  }

  &--catalog {
    padding-top: 0;

    .custom-select {
      min-width: 40px;
    }

    .custom-select__top {
      border: none;
      padding-bottom: 0;
      font-size: 13px;
      line-height: 1.2;
    }

    .custom-select__top::after {
      margin-top: 0;
    }

    .footer__top {
      margin-bottom: 0;
      border-top: 1px solid #eee;
      border-bottom: none;
      padding-top: 40px;
      padding-bottom: 40px;
    }

    .footer__bottom {
      padding-top: 20px;
      padding-bottom: 0;
    }

    @include desktop {
      .footer__social {
        order: 1;
      }

      .footer__menu {
        justify-content: center;
        order: 3;
        margin-top: 30px;
        width: 100%;
      }

      .footer__currency-lang {
        order: 2;
      }
    }

    @include small-tablet {
      .footer__top {
        flex-direction: column;
      }

      .footer__social {
        margin-bottom: 30px;
      }

      .footer__bottom {
        flex-wrap: wrap;

        picture {
          width: 100%;
          text-align: center;
        }
      }

      .footer__copyright {
        margin-bottom: 30px;
        width: 100%;
        text-align: center;
      }
    }
  }
}
